// COLORS

// Colors Primary
$BrightBlue: hsl(220, 98%, 61%);
$CheckBackground: linear-gradient
  (135deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%));

// Colors Light Theme
$VeryLightGray: hsl(0, 0%, 98%);
$VeryLightGrayishBlue: hsl(236, 33%, 92%);
$LightGrayishBlue: hsl(233, 11%, 84%);
$DarkGrayishBlue: hsl(236, 9%, 61%);
$VeryDarkGrayishBlue: hsl(235, 19%, 35%);

// Colors Dark Theme
$VeryDarkBlue: hsl(235, 21%, 11%);
$VeryDarkDesaturatedBlue: hsl(235, 24%, 19%);
$LightGrayishBlue: hsl(234, 39%, 85%);
$LightGrayishBlueHover: hsl(236, 33%, 92%);
$DarkGrayishBlue: hsl(234, 11%, 52%);
$VeryDarkGrayishBlue: hsl(233, 14%, 35%);
$VeryDarkGrayishBlue: hsl(237, 14%, 26%);

// FONTS

// Font Size
$FontSize: 18px;

// Font Family
$FontFamily: "Josefin Sans", sans-serif;

// Font Weights
$FW400: 400;
$FW700: 700;

// SCREEN WIDTH
$MobileWidth: 375px;
$TabletWidth: 768px;
$DesktopWidth: 1440px;

// IMAGES
$BgDesktopDark: "../images/bg-desktop-dark.jpg";
$BgMobileDark: "../images/bg-mobile-dark.jpg";
$BgDesktopLight: "../images/bg-desktop-light.jpg";
$BgMobileLight: "../images/bg-mobile-light.jpg";
$IconChecked: "../images/icon-check.svg";
